'use client'

import React, { useState, useEffect, ChangeEvent } from "react";
import { useRouter } from "next/navigation";
import { getToken, isAuthenticated } from "@/lib/utils/auth";
import { SidebarComponent } from "@/components/Sidebar/SidebarComponent";
import { fetchUserInfo, updateUserProfile } from "@/lib/api/userApi";
import { UserProfile, UpdateProfileData } from "@/types/api.types";
import { ArrowLeft, Camera } from "lucide-react";
import toast from "react-hot-toast";

export default function EditAccountPage() {
    const [user, setUser] = useState<UserProfile | null>(null);
    const [formData, setFormData] = useState({
        firstName: "",
        lastName: "",
        bio: "",
        imageUrl: "",
    });
    const [isLoading, setIsLoading] = useState(true);
    const [isSaving, setIsSaving] = useState(false);
    const [isMobile, setIsMobile] = useState(false);
    const [sidebarOpen, setSidebarOpen] = useState(false);
    const router = useRouter();

    // Check authentication
    useEffect(() => {
        if (typeof window === 'undefined') return;
        if (!isAuthenticated()) {
            router.push('/login');
        }
    }, [router]);

    // Check mobile
    useEffect(() => {
        if (typeof window === 'undefined') return;
        const checkMobile = () => {
            setIsMobile(window.innerWidth < 768);
        };
        checkMobile();
        window.addEventListener('resize', checkMobile);
        return () => window.removeEventListener('resize', checkMobile);
    }, []);

    // Fetch user info
    useEffect(() => {
        const token = getToken();
        if (!token) return;

        const loadUser = async () => {
            try {
                setIsLoading(true);
                const userInfo = await fetchUserInfo();
                setUser(userInfo);
                setFormData({
                    firstName: userInfo.firstName || "",
                    lastName: userInfo.lastName || "",
                    bio: userInfo.bio || "",
                    imageUrl: userInfo.imageUrl || "",
                });
            } catch (err) {
                console.error('Error fetching user:', err);
                toast.error('Could not load profile');
            } finally {
                setIsLoading(false);
            }
        };

        loadUser();
    }, []);

    // Handle input change
    const handleInputChange = (e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
        const { name, value } = e.target;
        setFormData(prev => ({ ...prev, [name]: value }));
    };

    // Handle save
    const handleSave = async () => {
        if (!user?.id) return;

        setIsSaving(true);
        try {
            await updateUserProfile(user.id, formData as UpdateProfileData);
            toast.success('Profile updated successfully');
            router.push('/profile');
        } catch (err) {
            console.error('Error updating profile:', err);
            toast.error('Could not update profile');
        } finally {
            setIsSaving(false);
        }
    };

    return (
        <div className="flex min-h-screen">
            {/* Mobile menu button */}
            {isMobile && (
                <button
                    onClick={() => setSidebarOpen(!sidebarOpen)}
                    className="fixed top-4 left-4 z-50 p-2 bg-white rounded-md shadow-md border md:hidden"
                    aria-label="Toggle menu"
                >
                    <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        {sidebarOpen ? (
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                        ) : (
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
                        )}
                    </svg>
                </button>
            )}

            {/* Mobile overlay */}
            {isMobile && sidebarOpen && (
                <div
                    className="fixed inset-0 bg-black bg-opacity-50 z-40 md:hidden"
                    onClick={() => setSidebarOpen(false)}
                />
            )}

            {/* Sidebar - Desktop */}
            <div className="hidden md:block w-[240px] h-screen bg-white border-r flex-shrink-0">
                <SidebarComponent />
            </div>

            {/* Sidebar - Mobile */}
            <div
                className={`
                    md:hidden fixed top-0 left-0 w-64 h-screen bg-white border-r z-50
                    transition-transform duration-300 ease-in-out
                    ${sidebarOpen ? 'translate-x-0' : '-translate-x-full'}
                `}
            >
                <SidebarComponent />
            </div>

            {/* Main Content */}
            <div className={`flex-1 min-h-screen w-full max-w-full overflow-x-hidden ${isMobile ? 'pt-16 px-4' : 'pl-3'}`}>
                <div className="min-h-screen bg-gradient-to-b from-gray-50 to-white">
                    <div className="max-w-2xl mx-auto px-4 py-8">
                        {/* Header */}
                        <div className="flex items-center gap-4 mb-8">
                            <button
                                onClick={() => router.back()}
                                className="p-2 hover:bg-gray-100 rounded-full transition-colors"
                            >
                                <ArrowLeft className="w-6 h-6 text-gray-600" />
                            </button>
                            <h1 className="text-2xl font-bold text-gray-800">Edit Profile</h1>
                        </div>

                        {isLoading ? (
                            <div className="bg-white rounded-2xl shadow-sm border border-gray-100 p-8 animate-pulse">
                                <div className="flex flex-col items-center gap-4">
                                    <div className="w-32 h-32 bg-gray-200 rounded-full" />
                                    <div className="w-full space-y-4 mt-6">
                                        <div className="h-12 bg-gray-200 rounded-lg" />
                                        <div className="h-12 bg-gray-200 rounded-lg" />
                                        <div className="h-24 bg-gray-200 rounded-lg" />
                                    </div>
                                </div>
                            </div>
                        ) : (
                            <div className="bg-white rounded-2xl shadow-sm border border-gray-100 p-8">
                                {/* Profile Image */}
                                <div className="flex flex-col items-center mb-8">
                                    <div className="relative group">
                                        <img
                                            src={formData.imageUrl || "https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_640.png"}
                                            alt="Profile"
                                            className="w-32 h-32 rounded-full object-cover border-4 border-white shadow-lg"
                                        />
                                        <div className="absolute inset-0 bg-black/40 rounded-full opacity-0 group-hover:opacity-100 transition-opacity flex items-center justify-center cursor-pointer">
                                            <Camera className="w-8 h-8 text-white" />
                                        </div>
                                    </div>
                                    <p className="text-sm text-gray-500 mt-3">Click to change photo</p>
                                </div>

                                {/* Form */}
                                <div className="space-y-6">
                                    <div className="grid grid-cols-2 gap-4">
                                        <div>
                                            <label className="block text-sm font-medium text-gray-700 mb-2">
                                                First Name
                                            </label>
                                            <input
                                                type="text"
                                                name="firstName"
                                                value={formData.firstName}
                                                onChange={handleInputChange}
                                                className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-sky-500 focus:border-transparent"
                                            />
                                        </div>
                                        <div>
                                            <label className="block text-sm font-medium text-gray-700 mb-2">
                                                Last Name
                                            </label>
                                            <input
                                                type="text"
                                                name="lastName"
                                                value={formData.lastName}
                                                onChange={handleInputChange}
                                                className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-sky-500 focus:border-transparent"
                                            />
                                        </div>
                                    </div>

                                    <div>
                                        <label className="block text-sm font-medium text-gray-700 mb-2">
                                            Image URL
                                        </label>
                                        <input
                                            type="text"
                                            name="imageUrl"
                                            value={formData.imageUrl}
                                            onChange={handleInputChange}
                                            placeholder="https://example.com/your-image.jpg"
                                            className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-sky-500 focus:border-transparent"
                                        />
                                    </div>

                                    <div>
                                        <label className="block text-sm font-medium text-gray-700 mb-2">
                                            Bio
                                        </label>
                                        <textarea
                                            name="bio"
                                            value={formData.bio}
                                            onChange={handleInputChange}
                                            rows={4}
                                            placeholder="Tell us about yourself..."
                                            className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-sky-500 focus:border-transparent resize-none"
                                        />
                                    </div>

                                    {/* Read-only fields */}
                                    {user && (
                                        <div className="bg-gray-50 rounded-xl p-4 space-y-3">
                                            <p className="text-sm text-gray-500">
                                                <span className="font-medium text-gray-700">Username:</span> @{user.username}
                                            </p>
                                            <p className="text-sm text-gray-500">
                                                <span className="font-medium text-gray-700">Email:</span> {user.email}
                                            </p>
                                        </div>
                                    )}

                                    {/* Actions */}
                                    <div className="flex gap-4 pt-4">
                                        <button
                                            onClick={() => router.back()}
                                            className="flex-1 py-3 px-6 border border-gray-200 text-gray-700 rounded-xl font-semibold hover:bg-gray-50 transition-colors"
                                        >
                                            Cancel
                                        </button>
                                        <button
                                            onClick={handleSave}
                                            disabled={isSaving}
                                            className="flex-1 py-3 px-6 bg-gradient-to-r from-sky-500 to-blue-600 text-white rounded-xl font-semibold hover:from-sky-600 hover:to-blue-700 disabled:opacity-50 disabled:cursor-not-allowed transition-all flex items-center justify-center"
                                        >
                                            {isSaving ? (
                                                <>
                                                    <svg className="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                                                        <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                                                        <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                                                    </svg>
                                                    Saving...
                                                </>
                                            ) : (
                                                "Save Changes"
                                            )}
                                        </button>
                                    </div>
                                </div>
                            </div>
                        )}
                    </div>
                </div>
            </div>
        </div>
    );
}
